/**
 * Logging utilities
 *
 * Handles logging to console and file system
 */

import { appendFile, mkdir } from "node:fs/promises";
import { dirname } from "node:path";
import { LOG_FILE } from "../config/constants.js";

type LogLevel = "debug" | "info" | "warn" | "error";

class Logger {
  private minLevel: LogLevel = "info";

  private levels: Record<LogLevel, number> = {
    debug: 0,
    info: 1,
    warn: 2,
    error: 3,
  };

  setLevel(level: LogLevel): void {
    this.minLevel = level;
  }

  private async writeToFile(message: string): Promise<void> {
    try {
      await mkdir(dirname(LOG_FILE), { recursive: true });
      const timestamp = new Date().toISOString();
      await appendFile(LOG_FILE, `[${timestamp}] ${message}\n`, "utf-8");
    } catch (error) {
      // Silently fail - don't crash if we can't write logs
      console.error("Failed to write to log file:", error);
    }
  }

  private shouldLog(level: LogLevel): boolean {
    return this.levels[level] >= this.levels[this.minLevel];
  }

  debug(message: string, ...args: unknown[]): void {
    if (this.shouldLog("debug")) {
      // Only write to file, don't clutter console
      this.writeToFile(`DEBUG: ${message} ${args.length > 0 ? JSON.stringify(args) : ""}`);
    }
  }

  info(message: string, ...args: unknown[]): void {
    if (this.shouldLog("info")) {
      // Only write to file, don't clutter console
      this.writeToFile(`INFO: ${message} ${args.length > 0 ? JSON.stringify(args) : ""}`);
    }
  }

  warn(message: string, ...args: unknown[]): void {
    if (this.shouldLog("warn")) {
      // Show warnings to user
      console.warn(`[Zest:Warn] ${message}`, ...args);
      this.writeToFile(`WARN: ${message} ${args.length > 0 ? JSON.stringify(args) : ""}`);
    }
  }

  error(message: string, error?: unknown): void {
    if (this.shouldLog("error")) {
      // Show errors to user
      console.error(`[Zest:Error] ${message}`, error);
      this.writeToFile(
        `ERROR: ${message} ${error instanceof Error ? error.stack : JSON.stringify(error)}`,
      );
    }
  }
}

export const logger = new Logger();
